:root {
  --main-bg-color: #151515;
  --main-font-color: #ff8c42;
  --second-font-color: #fff;
}

.dark-mode {
  --main-bg-color: #151515;
  --main-font-color: #ff8c42;
  --second-font-color: #fff;
}

.white-mode {
  --main-bg-color: #fff;
  --main-font-color: #ff5b00;
  --second-font-color: #7b3000;
}

.about {
  width: 100%;
  padding-block: 100px;
}
.about .container {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 50px;
}
.about .image {
  width: 453px;
  height: 507px;
}
.about .image img {
  width: 100%;
  height: 100%;
}
.about .text {
  margin-top: 50px;
}
.about .text h2 {
  font-size: 36px;
  color: var(--main-font-color);
  font-weight: 600;
  margin: 10px 25px;
  font-family: Arial, Helvetica, sans-serif;
}

.about .text p {
  font-size: 24px;
  font-weight: 600;
  line-height: 30px;
  margin: 30px 25px;
  font-family: Arial, Helvetica, sans-serif;
}
.dark-mode.about .text p {
  color: #fff;
}
.about .text .about-btn {
  width: 308px;
  height: 71px;
  background-color: rgba(255, 140, 66, 1);
  color: var(--main-bg-color);
  border: none;
  font-weight: 600;
  font-size: 24px;
  border-radius: 50px;
  text-transform: capitalize;
  margin-left: 100px;
  color: #fff;
  transition: 0.3s;
}
.about .text .about-btn:hover {
  box-shadow: 0 0 40px #ff8c42;
  cursor: pointer;
  transform: scale(1.1);
}


@media screen and (max-width: 1200px) {
 
  .about .container {
    flex-direction: column;
    gap: 0px;
  }
  .about .image {
    width: 95%;
    height: 95%;
    margin: 0 auto;
    
  }

  .about .text h2 {
    font-size: 2rem;
  }
  .about .text p {
    font-size: 1.2rem;
  }
  .about .text .about-btn {
    font-size: 16px;
    width: 200px;
    height: 56px;
  }
}
