footer {
  width: 100%;
  background: rgba(72, 72, 72, 1);
  padding: 30px 0px  0px;
  /* height: 100vh; */
}

.footer .images {
  width: 95%;
  height: 200px;
  /* padding: 50px 0; */
  margin:auto;
  display: flex;
  gap: 100px;
  flex-wrap: wrap;

}
.footer .images .logo {
  width: 156px;
  height: 146px;
}
.line {
  position: relative;
  left: -300px;
  top: 160px;
  width: 198px;
  height: 10px;
  background-color: rgba(255, 140, 66, 1);
}
.list {
  display: flex;
  align-items: center;
}
.footer .images .list .gallery {
  margin-right: 20px;
  cursor: pointer;
  transition: 0.3s;
  width: 125px;
  height: 126px;
}
.footer .images .list img:hover {
  transform: scale(1.3);
}
/* .footer .images .arrow {
  width: 50px;
  height: 50px;
} */

.line2 {
  position: relative;
  right: -130%;
  top: 30px;
  width: 198px;
  height: 10px;
  background-color: rgba(255, 140, 66, 1);
}
.menu {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 100px;
  flex-wrap: wrap;
  position: relative;
  top: 0px;
  margin-bottom: 35px;
}
.menu h3 {
  font-size: 18px;
  font-weight: 600;
  font-family: Quicksand;
  text-transform: capitalize;
  color: rgba(255, 140, 66, 1);
}

.about1 {
  width: 25%;
  display: flex;
  flex-direction: column;
}
.about1 p {
  font-size: 18px;
  font-weight: 300;
  font-family: Quicksand;
  color: rgba(255, 255, 255, 1);
  margin: 25px 0 0px -35px;
}
.about1 h3 {
  /* margin-bottom: 25px; */
}
.social-icons {
  width: 32px;
  height: 32px;
  display: flex;
  gap: 20px;
  /* margin-bottom: 0px; */
  cursor: pointer;
  transition: 0.3s;
}
.social-icons img:hover {
  transform: scale(1.1);
}
.social-icons-single {
  padding-top: 20px;
}
.Liferzeiten-style h3,
.Öffnungszeiten-style h3 {
  margin: 10px 15px;
}
.Öffnungszeiten-cont p,
.Liferzeiten-cont p {
  color: #fff;
  font-size: 18px;
  font-family: Quicksand;
  font-weight: 700;
}
.Liferzeiten-cont,
.Öffnungszeiten-cont {
  display: flex;
}
.Liferzeiten-row,
.Öffnungszeiten-row {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.space {
  padding-left: 40px;
}
.Öffnungszeiten span,
.Liferzeiten span {
  color: rgba(255, 140, 66, 1);
}
.copy-right-cont {
  
  display: flex;
  padding: 0px 30.5%;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  font-family: Arial;
  cursor: pointer;
 width: 100%;
 height: 25px;

}
.copy-right-cont h2 {
  margin-top: -4px;
 
}
.copyRight {
  font-weight: 100;

}
.category p {
  color: #fff;
  font-size: 18px;
  margin-bottom: 10px;
  font-family: Arial, Helvetica, sans-serif;
  transition: 0.4s linear;
  margin-top: 8px;
  cursor: pointer;
}
.category p:hover {
  box-shadow: 0 0 50px 10px #fff;
  padding: 4px;
  background-color: rgba(255, 140, 66, 1);
  border-radius: 12px;
}

@media screen and (max-width: 768px) {
  footer {
    padding: 70px 0px  0px;
    /* height: 100vh; */
  }
  .footer .images {
    width: 95%;
    height: 400px;
    
  
  }
  .footer .images .list .gallery {
    width: 60px;
  }
  .footer .images .arrow {
    width: 25px;
    height: 25px;
  }
  .about1 p {
    display: none;
  }
  .about1 img {
    width: 15px;
  }
  .copy-right-cont {
    width: 450px;
    height: 52px;
  }
  .category p {
    color: #fff;
    font-size: 14px;
    margin-bottom: 10px;
    font-family: Arial, Helvetica, sans-serif;
  }
  .copy-right-cont {
    gap: 5px;
    padding: 0px 4% 0 0;
  }
  .copy-right-cont .copyRight {
    font-size: 15px;
    font-weight: 200;
    width: 300px;
  }
}
